import React from "react";
import styled from "@emotion/styled";
import { ReactComponent as CogIcon } from "../Assets/cog.svg";
import WeatherIcon from "./WeatherIcon";
import { ReactComponent as AirFlowIcon } from "../Assets/airFlow.svg";
import { ReactComponent as RainIcon } from "../Assets/rain.svg";
import { ReactComponent as LoadingIcon } from "../Assets/loading.svg";
import { ReactComponent as RefreshIcon } from "../Assets/refresh.svg";

interface WeatherCardProps {
  theme?: any;
}

const WeatherCardWrapper = styled.div<WeatherCardProps>`
  position: relative;
  width: 100%;
  height: 100%;
  // box-shadow: ${({ theme }) => theme.boxShadow};
  box-shadow: 0 1px 3px 0 #999999;
  background-color: ${({ theme }) => theme.foregroundColor};
  box-sizing: border-box;
  padding: 10px 15px;
`;

const Cog = styled(CogIcon)`
  position: absolute;
  top: 23px;
  right: 15px;
  width: 15px;
  height: 15px;
  cursor: pointer;
`;

const Location = styled.div<WeatherCardProps>`
  font-size: 20px;
  // color: ${({ theme }) => theme.titleColor};
  color: #757575;
  margin-bottom: 20px;
`;

const Description = styled.div<WeatherCardProps>`
  font-size: 13px;
  // color: ${({ theme }) => theme.textColor};
  color: #828282;
`;

const CurrentWeather = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: -8px;
`;

const Temperature = styled.div<WeatherCardProps>`
  // color: ${({ theme }) => theme.temperatureColor};
  color: #757575;
  font-size: 68px;
  font-weight: 300;
  display: flex;
`;

const Celsius = styled.div`
  font-weight: normal;
  font-size: 21px;
`;

const AirFlow = styled.div<WeatherCardProps>`
  display: flex;
  align-items: center;
  font-size: 12x;
  font-weight: 300;
  // color: ${({ theme }) => theme.textColor};
  color: #828282;
  margin-bottom: 8px;

  svg {
    width: 25px;
    height: auto;
    margin-right: 30px;
  }
`;

const Rain = styled.div<WeatherCardProps>`
  display: flex;
  align-items: center;
  font-size: 12x;
  font-weight: 300;
  // color: ${({ theme }) => theme.textColor};
  color: #828282;

  svg {
    width: 25px;
    height: auto;
    margin-right: 30px;
  }
`;

interface RefreshStyledProps {
  theme?: any;
  isLoading?: any;
}

const Refresh = styled.div<RefreshStyledProps>`
  position: absolute;
  right: 15px;
  bottom: 15px;
  font-size: 12px;
  display: inline-flex;
  align-items: flex-end;
  // color: ${({ theme }) => theme.textColor};
  color: #828282;

  svg {
    margin-left: 10px;
    width: 15px;
    height: 15px;
    cursor: pointer;
    animation: rotate infinite 1.5s linear;
    animation-duration: ${({ isLoading }) => (isLoading ? "1.5s" : "0s")};
  }
  @keyframes rotate {
    from {
      transform: rotate(360deg);
    }
    to {
      transform: rotate(0deg);
    }
  }
`;

interface WeatherCardProps {
  weatherElement?: any;
  moment?: any;
  fetchData?: any;
  setCurrentPage?: any;
  cityName?: string;
}

const WeatherCard: React.FC<WeatherCardProps> = (props) => {
  const { weatherElement, moment, fetchData, setCurrentPage, cityName } = props;

  const {
    observationTime,
    temperature,
    windSpeed,
    description,
    weatherCode,
    rainPossibility,
    comfortability,
    isLoading,
  } = weatherElement;

  return (
    <WeatherCardWrapper>
      <Cog onClick={() => setCurrentPage("WeatherSetting")} />

      <Location>{cityName}</Location>

      <Description>
        {description} {comfortability}
      </Description>

      <CurrentWeather>
        <Temperature>
          {Math.round(temperature)} <Celsius>C</Celsius>
        </Temperature>

        <WeatherIcon
          currentWeatherCode={weatherCode}
          moment={moment || "day"}
        />
      </CurrentWeather>

      <AirFlow>
        <AirFlowIcon />
        {windSpeed} m/h
      </AirFlow>

      <Rain>
        <RainIcon />
        {Math.round(rainPossibility)} %
      </Rain>

      <Refresh onClick={fetchData} isLoading={isLoading}>
        
        {new Intl.DateTimeFormat("zh-TW", {
          hour: "numeric",
          minute: "numeric",
        }).format(new Date(observationTime))}{" "}
        {isLoading ? <LoadingIcon /> : <RefreshIcon />}
      </Refresh>
    </WeatherCardWrapper>
  );
};

export default WeatherCard;
