interface ContainerProps {
  theme?: any;
}

const Container = styled.div<ContainerProps>`
  background-color: ${({ theme }) => theme.backgroundColor};
  width: 360px;
  height: 252px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 3rem;
`;

const getMoment = (locationName: string) => {
  const location = sunriseAndSunsetData.find(
    (data) => data.locationName === locationName
  );
  if (!location) return null;

  const now = new Date();

  const nowDate = Intl.DateTimeFormat("zh-TW", {
    year: "numeric",
    month: "2-digit",
    day: "2-digit",
  })
    .format(now)
    .replace(/\//g, "-");

  const locationDate = location.time.find((time) => time.dataTime === nowDate);

  if (!locationDate) {
    throw new Error(
      ` ${locationName}  ${nowDate}  'https://github.com/pjchender/learn-react-from-hook-realtime-weather-app#'`
    );
  }

  const sunriseTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunrise}`
  ).getTime();

  const sunsetTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunset}`
  ).getTime();

  const nowTimeStamp = now.getTime();

  return sunriseTimestamp <= nowTimeStamp && nowTimeStamp <= sunsetTimestamp
    ? "day"
    : "night";
};

const WeatherApp = () => {
  const [currentCity, setCurrentCity] = useState<string>("");
  const currentLocation: any = findLocation(currentCity) || {};
  const [weatherElement, fetchData] = useWeatherApi(currentLocation);
  const [currentTheme, setCurrentTheme] = useState<string>("light");
  const [currentPage, setCurrentPage] = useState<string>("WeatherCard");

  const moment = useMemo(
    () => getMoment(currentLocation.sunriseCityName),
    [currentLocation.sunriseCityName]
  );

  useEffect(() => {
    setCurrentTheme(moment === "day" ? "light" : "dark");
  }, [moment]);

  return (
    <Container>
      {currentPage === "WeatherCard" && (
        <WeatherCard
          cityName={currentLocation.cityName}
          weatherElement={weatherElement}
          moment={moment}
          fetchData={fetchData}
          setCurrentPage={setCurrentPage}
        />
      )}

      {currentPage === "WeatherSetting" && (
        <WeatherSetting
          cityName={currentLocation.cityName}
          setCurrentCity={setCurrentCity}
          setCurrentPage={setCurrentPage}
        />
      )}
    </Container>

    // </ThemeProvider>
  );
};

export default WeatherApp;
